.page-template .main {
  min-height: calc(100vh - 7rem); }

.page-template button {
  -webkit-transition: all .15s ease-in;
  transition: all .15s ease-in;
  background: white;
  border: 1px solid rgba(73, 80, 87, 0.9);
  color: rgba(73, 80, 87, 0.9);
  height: 30px;
  border-radius: 0.2rem;
  cursor: pointer;
  font-size: 0.74rem; }

.page-template button:hover {
  border: 1px solid #212529;
  color: #212529; }

.page-template button:focus {
  border: 1px solid #212529;
  color: #212529; }

.page-template button:active {
  border: 1px solid #212529;
  color: #212529; }

.page-template button + button {
  margin-left: 0.1rem; }

.page-template .top-img {
  text-align: center; }
  .page-template .top-img .img-cover {
    background-color: black;
    color: white;
    position: relative; }
    .page-template .top-img .img-cover img {
      max-width: 100%;
      height: auto; }
    .page-template .top-img .img-cover .img-title {
      position: absolute;
      bottom: 0px;
      margin: 0.6rem;
      font-weight: bold; }
      .page-template .top-img .img-cover .img-title .img-title01 {
        display: inline-block;
        font-size: 0.64rem;
        border-bottom: 0.2px solid white;
        margin-right: 20px; }
        @media (max-width: 419px) {
          .page-template .top-img .img-cover .img-title .img-title01 {
            margin-right: 10px; } }
      .page-template .top-img .img-cover .img-title .img-title-sub {
        display: inline-block;
        margin-top: 10px;
        font-size: 1.4rem; }
        @media (max-width: 419px) {
          .page-template .top-img .img-cover .img-title .img-title-sub {
            font-size: 1rem; } }
